mayor será el monto acumulado al final del periodo. Por ejemplo, un capital con intereses compuestos capitalizados mensualmente crecerá más rápido que uno con capitalización anual, incluso si la tasa de interés anual es la misma, puesto que los intereses se reinvierten con mayor frecuencia, aumentando el monto base para el siguiente cálculo.")])]),_c('div',{staticClass:"bg-color-white box-shadow p-3",attrs:{"x":"14%","y":"12%","numero":"+"}},[_c('h5',[_vm._v("3. Ejemplo")]),_c('p',[_vm._v("El impacto del interés compuesto es particularmente notable en inversiones a largo plazo. Por ejemplo, una persona que invierte $10,000 a una tasa de interés anual del 5 % durante 20 años verá un crecimiento mucho mayor con interés compuesto que con interés simple. Mientras que el interés simple generará un crecimiento lineal de $1,000 por año, el interés compuesto hará que el capital crezca exponencialmente, alcanzando un monto significativamente superior gracias a la acumulación de intereses sobre intereses.")])]),_c('div',{staticClass:"bg-color-white box-shadow p-3",attrs:{"x":"87%","y":"12%","numero":"+"}},[_c('h5',[_vm._v("4. Utilización")]),_c('p',[_vm._v("El interés compuesto es ampliamente utilizado en diversas áreas financieras, como en cuentas de ahorro, planes de retiro, inversiones en bonos o acciones, y en productos financieros como certificados de depósito y fondos mutuos. Su capacidad para generar rendimientos exponenciales lo convierte en una opción preferida tanto para individuos como para instituciones financieras que buscan maximizar el crecimiento de sus capitales.")])]),_c('div',{staticClass:"bg-color-white box-shadow p-3",attrs:{"x":"100%","y":"38%","numero":"+"}},[_c('h5',[_vm._v("5. Ámbito del endeudamiento")]),_c('p',[_vm._v("Además de ser una herramienta poderosa para los inversionistas, el interés compuesto también tiene aplicaciones en el ámbito del endeudamiento. Por ejemplo, en préstamos como hipotecas o tarjetas de crédito, el interés compuesto puede